import ScrollReveal from "scrollreveal";
ScrollReveal({
  easing: "ease",
});


ScrollReveal().reveal('.faq__list--home .faq__item', {
  duration: 1000,
  distance: '20px',
  origin: 'top',
  interval: 1400,
  beforeReveal: (el) => {
    el.classList.add('revealed');
  }
});


ScrollReveal().reveal(".header--home", {
  duration: 600,
  distance: "100%",
  delay: 600,
  origin: "top"
});


ScrollReveal().reveal(".hero__content > h1", {
  duration: 700,
  distance: "20px",
  delay: 320,
  origin: "top"
});

ScrollReveal().reveal(".hero__content > .btn", {
  duration: 800,
  distance: "30px",
  delay: 650,
  origin: "bottom"
});

ScrollReveal().reveal(".hero__inner > .social", {
  duration: 1300,
  distance: "20px",
  delay: 1150,
  origin: "top"
});

ScrollReveal().reveal(".hero__inner > .lang", {
  duration: 1300,
  distance: "20px",
  delay: 1450,
  origin: "top"
});


